import { loader } from "graphql.macro";
import { useQuery } from "@apollo/client";
import { UserQuery } from "../models";

const userQuery = loader("../queries/User.gql");
const queryLimit = 20;

export const useUsers = (query: string) => {
  const { data, fetchMore, ...rest } = useQuery(userQuery, {
    variables: {
      query,
      first: queryLimit,
    },
  });

  const getMore = (variables: UserQuery) =>
    fetchMore({
      variables: {
        query,
        ...variables,
      },
      updateQuery: (prev, { fetchMoreResult }) => fetchMoreResult || prev,
    });

  const nextPage = () =>
    getMore({
      first: queryLimit,
      after: data?.search?.pageInfo.endCursor,
    });

  const prevPage = () =>
    getMore({
      last: queryLimit,
      first: undefined,
      before: data?.search?.pageInfo.startCursor,
    });

  return {
    users: data?.search?.nodes,
    nextPage,
    prevPage,
    ...data?.search,
    ...rest,
  };
};
